import Vue from 'vue'
import Router from 'vue-router'
import Jianli from './jianli.js'

Vue.use(Router)

const router = new Router({
  routes: [
    Jianli,
    {
      path: '/',
      redirect: '/jl'
    }

  ],
  linkActiveClass: 'active'
});
