<template>
  <div id="app">
    <router-view></router-view>
    <!--<div style="position: absolute;top:0;right:0;left:0;bottom:0;"></div>-->
  </div>
</template>
<!--引入scss-->
<style lang="scss" src="@/assets/scss/style.scss"></style>
<script>
  // 新字体图标库
//  import "./assets/font/iconfont.css"
  //animation.css
//  import "animate.css"
  import {mapState, mapActions, mapGetters} from 'vuex'
  export default {
    name: 'app',

    computed: {
      ...mapState({
        screenWidth: state => state.screenWidth,
        screenHeight: state => state.screenHeight,
        showLoading: state => state.status.showLoading,
      }),
    },
    watch: {
      screenWidth (val) {
        this.setScreenWidth(val)
      },
      screenHeight (val) {
        this.setScreenHeight(val)
      }
    },
    methods: {
      ...mapActions({
        setScreenWidth: 'setScreenWidth',
        setScreenHeight: 'setScreenHeight'
      }),
    },
    mounted () {
      window.screenWidth = document.body.clientWidth
      window.screenHeight = document.body.clientHeight
      this.setScreenWidth(window.screenWidth)
      this.setScreenHeight(window.screenHeight)
      const that = this
      window.onresize = () => {
        return (() => {
          window.screenWidth = document.body.clientWidth
          window.screenHeight = document.body.clientHeight
          this.setScreenWidth(window.screenWidth)
          this.setScreenHeight(window.screenHeight)
        })()
      }
    }
  }
</script>
<style>
  body, html {
    background-image: url('./assets/images/default/index_bg.jpg');
    background-size: cover;
    background-position: 50% 50%;
    background-attachment: fixed;
    overflow: hidden;
    padding: 0 !important;
  }

  #app {
    width: 100%;
    height: 100%;
    /*background: url('');*/

  }
</style>





